html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.content {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.content-sm {
  max-width: 55rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 2.8rem;
}

h2 {
  font-size: 2rem;
}

p {
  font-size: 1.6rem;
  color: #111;
  line-height: 1.8;
}

a {
  color: #767676;
  text-decoration: underline;
}

.description {
  text-align: center;
}
.description img {
  height: 4.5rem;
  margin-bottom: 2rem;
}

/** Media Queries **/
.login .alert.error {
  background-color: #e25c3d;
  padding: 2rem 0;
}
.login .alert.error ul {
  display: inline;
  text-align: center;
}
.login .alert.error ul li {
  list-style: none;
  color: #FFFFFF;
}
.login .content {
  margin-top: 5rem;
  position: relative;
  min-height: 100vh;
}
.login .actions {
  width: 100%;
  position: absolute;
  bottom: 0;
  height: 11rem;
  padding: 1rem;
  border: 1px solid #c3c3c3;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .login .actions {
    width: 55rem;
  }
}
.login .actions .login__alt-cta__img {
  position: absolute;
  pointer-events: none;
  stroke: #d1d1d1;
  fill: none;
  opacity: 0.8;
  stroke-width: 1.5;
  stroke-linecap: round;
}
.login .actions .login__alt-cta__img01 {
  left: 4%;
  bottom: -10px;
}
.login .actions .login__alt-cta__img02 {
  right: 5%;
  top: 0;
}

@media (min-width: 1024px) {
  .join {
    display: grid;
    grid-template-columns: 35% 65%;
    height: 100vh;
  }
}
.join .content-sm {
  padding: 2rem;
}
@media (min-width: 1024px) {
  .join .content-sm {
    margin-top: 5rem;
    width: 60rem;
  }
}
.join .content-sm .label-justify {
  display: block;
  margin-bottom: 0.5rem;
}
@media (min-width: 768px) {
  .join .content-sm .form .group {
    display: flex;
    justify-content: space-between;
  }
}
.join .img-join {
  background-image: url("https://images.unsplash.com/photo-1682685797229-b2930538da47?ixlib=rb-4.0.3&ixid=M3wxMjA3fDF8MHxlZGl0b3JpYWwtZmVlZHwxfHx8ZW58MHx8fHx8&auto=format&fit=crop&w=400&q=60");
  background-repeat: no-repeat;
  text-align: center;
  background-position: center center;
  background-size: cover;
  height: 25rem;
  position: relative;
}
@media (min-width: 1024px) {
  .join .img-join {
    height: auto;
  }
}
.join .img-join:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
.join .img-join .img-join-content {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  padding: 3rem;
}
.join .img-join .img-join-content h2, .join .img-join .img-join-content p {
  color: #FFFFFF;
  position: relative;
  font-weight: bold;
}
@media (min-width: 768px) {
  .join .img-join .img-join-content h2 {
    font-size: 4.8rem;
  }
  .join .img-join .img-join-content p {
    font-size: 2.4rem;
  }
}

.forgot {
  margin-top: 10rem;
}

.message {
  margin-top: 10rem;
}

.confirm {
  text-align: center;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
.confirm .img-confirm {
  height: 100vh;
}
.confirm .content-sm {
  margin-top: 10rem;
}

.reset {
  margin-top: 10rem;
}

.form {
  margin-top: 2rem;
}
.form .row {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.form label {
  margin-bottom: 0.6rem;
}
.form .label-justify {
  display: flex;
  justify-content: space-between;
}
.form input:not([type=submit]) {
  width: 100%;
  height: 4rem;
  padding: 0.6rem 1.2rem;
  line-height: 1.6;
  color: #111;
  border: 1px solid #767676;
  border-radius: 0.3rem;
  background-color: transparent;
}
.form input[type=submit] {
  background-color: #111;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .form input[type=submit] {
    width: 100%;
  }
}
.form input[type=submit]:hover {
  background-color: black;
  cursor: pointer;
}

.alerts {
  padding: 2rem 2rem 0.5rem;
  margin-bottom: 2rem;
  border-radius: 0.4rem;
}
.alerts.error {
  background-color: #f9ded8;
}
.alerts.success {
  background-color: #82d5a4;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .header-home {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header .header-home {
    align-items: center;
    flex-direction: row;
  }
}
.header .header-home .header-home-input {
  height: 5.5rem;
  width: 23rem;
  border: 1px solid #BDBDBD;
  border-radius: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .header-home .header-home-input {
    width: 30rem;
  }
}
.header .header-home .header-home-input:hover {
  box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.2784313725);
  border-color: rgba(223, 225, 229, 0);
}
.header .header-home .header-home-input .input-search {
  width: 1.7rem;
  height: 1.7rem;
}
.header .header-home .header-home-input .input-search:hover {
  cursor: pointer;
}
.header .header-home .header-home-input input {
  border: none;
  outline: none;
  height: 4rem;
  width: 18rem;
}
@media (min-width: 768px) {
  .header .header-home .header-home-input input {
    width: 25rem;
  }
}
.header .header-action-user-desktop {
  display: none;
}
@media (min-width: 1024px) {
  .header .header-action-user-desktop {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
}
.header .header-action-user-desktop .btn {
  background-color: #3DB46D;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 13.7rem;
  border-radius: 1.2rem;
  height: 5.5rem;
  padding: 0;
}
@media (min-width: 768px) {
  .header .header-action-user-desktop .btn {
    width: 13.7rem;
  }
}
.header .header-action-user-desktop .btn:hover {
  background-color: #308e56;
  cursor: pointer;
}
.header .header-action-user {
  position: absolute;
  top: 0.5rem;
  right: 1rem;
}
.header .header-action-user #hamburger:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .header .header-action-user {
    display: none;
  }
}

.action-user-contentList {
  padding: 9rem 3rem;
  background-color: #FFFFFF;
  position: absolute;
  right: -1rem;
  top: 6rem;
  border-radius: 2rem;
  border: 1px solid #767676;
  z-index: 1;
}
.action-user-contentList .btn {
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #111;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 80%;
  position: absolute;
  padding: 0.5rem;
  bottom: 1rem;
  left: 50%;
  transform: translate(-50%, 0);
  border: 1px solid #767676;
}
@media (min-width: 768px) {
  .action-user-contentList .btn {
    width: 80%;
  }
}
.action-user-contentList .btn:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}

.hide {
  display: none;
}

.content-grid-photos {
  margin-top: 2rem;
  display: grid;
  align-items: start;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(1, minmax(0, 1fr));
}
@media (min-width: 768px) {
  .content-grid-photos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media (min-width: 1024px) {
  .content-grid-photos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
.content-grid-photos .grid-row {
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  row-gap: 0.5rem;
}

figure {
  position: relative;
  height: auto;
  margin: 0;
}
figure .deleteImg,
figure span {
  position: absolute;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
figure span {
  width: 20rem;
  color: #FFFFFF;
  bottom: 2rem;
  padding: 1rem;
  left: 50%;
  font-size: 1.4rem;
  transform: translate(-50%, 0);
}
@media (min-width: 768px) {
  figure span {
    width: 25rem;
    bottom: 1rem;
    font-size: 1.8rem;
  }
}
figure .deleteImg {
  top: 2rem;
  background-color: transparent;
  width: 6.3rem;
  height: 2.3rem;
  border: 1px solid #ae1515;
  border-radius: 3.8rem;
  color: #ae1515;
  font-size: 1rem;
  text-align: center;
  padding: 0.5rem;
  right: 1rem;
}
figure:hover span, figure:hover .deleteImg {
  cursor: pointer;
  opacity: 1;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
}
.modal .alerts {
  max-width: 62rem;
  margin: 0 auto;
  transform: translateY(15rem);
}
.modal .form {
  max-width: 35rem;
  height: 32.72rem;
  border-radius: 1.2rem;
  padding: 3rem 2.5rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 600ms;
  transition-timing-function: ease-out;
  opacity: 0;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .modal .form {
    max-width: 62rem;
  }
}
.modal .form .row {
  margin-top: 2rem;
  color: #767676;
}
@media (min-width: 768px) {
  .modal .form .row {
    width: 52.3rem;
  }
}
.modal .form legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .form input[type=text] {
  border: 1px solid #767676;
  border-radius: 0.5rem;
}
.modal .form .form-option {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
  color: #767676;
}
.modal .form .form-option input {
  background-color: #3DB46D;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 15rem;
  height: 5rem;
  border-radius: 1.2rem;
}
@media (min-width: 768px) {
  .modal .form .form-option input {
    width: 15rem;
  }
}
.modal .form .form-option input:hover {
  background-color: #308e56;
  cursor: pointer;
}
.modal .form.animated {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .form.animated:hover {
  cursor: pointer;
}
.modal .form.close {
  transform: translateY(-200%);
  opacity: 0;
}
.modal .deletePhoto {
  height: 25.72rem;
}
.modal .deletePhoto .form-option input {
  background-color: #e25c3d;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 15rem;
}
@media (min-width: 768px) {
  .modal .deletePhoto .form-option input {
    width: 15rem;
  }
}
.modal .deletePhoto .form-option input:hover {
  background-color: #cd3f1f;
  cursor: pointer;
}
.modal .deletePhoto .alert {
  transform: translateY(20rem);
}